import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/components/main'
import Upcoming from '../components/upcoming'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'main',
      component: Main
    },
    {
      path: '/upcoming',
      name: 'upcoming',
      component: Upcoming
    }
  ]
})



